.hero-outer {
	padding: 0;
	position: relative;
	box-sizing: border-box;
	height: 300px;
	padding: 0;
	overflow: hidden;

	@include respond-max($mobile-max) {
		height: auto;
	}

	.container {
		height: 100%;
		position: relative;
		z-index: 0;
		display: flex;
		align-items: center;

		@include respond-max($mobile-max) {
			padding: 42px 15px;
		}

		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			height: 1px;
			width: 100%;
			background-color: $midnight-blue-5;
		}

		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			height: 1px;
			width: 100%;
			background-color: $midnight-blue-5;
		}
	}

	&:before {
		content: '';
		width: 400px;
		height: 1px;
		position: absolute;
		top: 0;
		left: -10%;
		display: inline-block;
		background-image: url(../images/hero_border_shine.png);
		animation: anim1 90s infinite;
		opacity: 0;
		z-index: 1;
	}

	&:after {
		content: '';
		width: 400px;
		height: 1px;
		position: absolute;
		bottom: 0;
		right: -10%;
		display: inline-block;
		background-image: url(../images/hero_border_shine.png);
		animation: anim2 90s infinite;
		opacity: 0;
		z-index: 1;
	}

	.hero-inner {
		width: 100%;

		h1 {
			@include font-size(72px);
			margin-bottom: 10px;
			color: $midnight-blue-5;
			font-family: $code-light-font;

			@include respond-max($mobile-max) {
				@include font-size(52px);
			}
		}

		h2 {
			@include font-size(12px);
			text-transform: none;
			color: $midnight-blue-4;
			display: inline-block;
		}
	}
}

@keyframes anim1 {
    0% {
    	left: -10%;
    	opacity: 0;
    }
    5% {
    	opacity: 1;
    }
    15% {
    	opacity: 1;
    }
    25% {
    	opacity: 0.85;
    }
    50% {
    	left: 90%;
    	opacity: 1;
    }
    75% {
    	opacity: 0.85;
    }
    100% {
    	opacity: 0;
    	left: -10%;
    }
}

@keyframes anim2 {
    0% {
    	right: -10%;
    	opacity: 0;
    }
    10% {
    	opacity: 0.85;
    }
    20% {
    	opacity: 1;
    }
    35% {
    	opacity: 1;
    }
    50% {
    	right: 90%;
    	opacity: 1;
    }
    65% {
    	opacity: 0.85;
    }
    80% {
    	opacity: 1;
    }
    100% {
    	opacity: 0;
    	right: -10%;
    }
}