.post-outer {
	padding-top: $standard-padding-top;
	
	.post-inner {
		
		.blog-post {
			padding-right: 5%;
			margin-bottom: 3em;

			h1 {
				color: #000;
			}

			.lead {
				margin-top: 23px;
				
				a {
    				font-family: $commadore-font;
    				font-weight: bold;
				}
			}

			.post {
				padding-top: 1em;
			}

			.blog-back {
				margin-top: 1em;
			}
		}

		.blog-info {

			@include respond-max($mobile-max) {
				display: none;
			}

			h1 {
				margin-bottom: 23px;
				color: #000;
			}
		}
	}
}