.blog-outer {
	padding-top: $standard-padding-top;
	
	.blog-inner {
		
		.blog-listing {
			padding-right: 5%;
			margin-bottom: 3em;

			h1 {
				color: #000;
			}

			.lead {

				a {
    				font-family: $commadore-font;
    				font-weight: bold;
				}
			}

			.pagination-outer {
				margin-top: 1em;

				.pagination {
					display: inline-block;	

					li {
						display: inline-block;
					}
				}
			}

			.post {
				border-top: 1px solid #7A0000;
				padding: 0 0 16px;

				&:first-of-type {
					border-top: 0;

					h2 {
						padding-top: 0;
					}
				}

				h2 {
					@include font-size(28px);
					margin-bottom: 12px;
					padding-top: 14px;

					a {
						@include font-size(28px);
						color: #073642;
						text-decoration: none;
					}
				}
			}
		}

		.blog-info {

			@include respond-max($mobile-max) {
				display: none;
			}

			h1 {
				margin-bottom: 23px;
				color: #000;
			}
		}
	}
}